import React from "react";

const workspaceConfiguration = [
    // API Key
    {
        path: "/api-keys",
        component: React.lazy(() => import("./api-key/ViewAPIKeys")),
    },

    // Webhooks
    {
        path: "/webhooks",
        component: React.lazy(() => import("./webhook/ViewWebhooks")),
    },

    // Screen
    {
        path: "/screens/:identifier",
        component: React.lazy(() => import("./screen/ViewScreens")),
    },
    {
        path: "/screens",
        component: React.lazy(() => import("./screen/ViewScreens")),
        exact: true,
    },

    // Action
    {
        path: "/actions/:identifier",
        component: React.lazy(() => import("./action/ViewActions")),
    },
    {
        path: "/actions",
        component: React.lazy(() => import("./action/ViewActions")),
        exact: true,
    },

    // Model
    {
        path: "/models/:identifier",
        component: React.lazy(() => import("./model/ViewModels")),
    },
    {
        path: "/models",
        component: React.lazy(() => import("./model/ViewModels")),
        exact: true,
    },

    // Analytics
    {
        path: "/dashboard",
        component: React.lazy(() => import("./dashboard/Dashboard")),
        exact: true,
    },

    // Preferences
    {
        path: "/preferences",
        component: React.lazy(() => import("./preferences/Preferences")),
        exact: true,
    },
];

export default workspaceConfiguration;
